/*MOBILE VERSION*/
@media screen and (max-width: 425px) {

}

/* FREELANCE CONTENT */
@media screen and (max-width: 640px) {
    .project_card {
        width: 90vw;
        height: 90vh;
        font-size: small;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        padding-top: 10px;
    }

    .image {
        width: 80px;
        margin-left: -80px;
        margin-top: -90px;
    }
}

@media screen and (min-width: 768px) {
    .project_card {
        width: 50vw;
        height: 60vh;
    }

    .image {
        width: 125px;
        margin-top: -70px;
        margin-left: -50px;
    }
    #interest{
        background: rgb(255,255,255);
        background: linear-gradient(180deg, rgba(255,255,255,1) 70%, rgba(31,41,55,1) 70%, rgba(31,41,55,1) 100%);
      }
}

@media screen and (min-width: 1024px) {
    .image {
        width: 150px;
        margin-left: -10px;
    }

    body,
    html {
        overflow-x: visible;
    }
}

@media screen and (max-width: 768px) {
    /* #background_image{
      background-position: center;
    } */

    .title_text::before {
        height: 5px;
    }

    #interest {
        background: #fff;
    }

    .subtitle_text::before {
        content: "";
        height: 5px;
        bottom: 7px;
    }

}

/* FUTURE SECTION */
@media screen and (min-width: 640px) {
    #future {
        padding-left: 25vw;
    }
}